: 220px;\n  right: 250px;\n}\n@media (max-width: 1199px) {\n  img.arrow-top {\n    display: none;\n  }\n}\n\nimg.arrow-bottom {\n  position: absolute;\n  bottom: 100px;\n  left: 250px;\n}\n@media (max-width: 1199px) {\n  img.arrow-bottom {\n    display: none;\n  }\n}\n\n/* service single start */\n.service-menu li:not(:last-child) a {\n  border-bottom: 1px solid #e5e5e5;\n}\n.service-menu li a:hover, .service-menu li a.active {\n  color: #fff !important;\n  background: #303032;\n}\n\n/* service single end */\n/* 15. work section */\n.work {\n  /* 15.1 work slider */\n}\n.work-slider {\n  /* 15.2 slick dots */\n}\n.work-slider-image {\n  position: relative;\n}\n.work-slider-image .image-overlay {\n  position: absolute;\n  top: 0;\n  left: 0;\n  background: rgba(0, 0, 0, 0.75);\n  height: 100%;\n  width: 100%;\n  padding: 15px 25px;\n  opacity: 0;\n  transition: 0.2s;\n}\n.work-slider-image .image-overlay i {\n  position: absolute;\n  color: #fff;\n  top: 50%;\n  left: 50%;\n  transform: translate(-50%, -50%);\n  display: inline-block;\n  font-size: 24px;\n  transition: 0.2s ease;\n  padding: 10px;\n}\n.work-slider-image .image-overlay i:hover {\n  font-size: 30px;\n}\n.work-slider-image .image-overlay a.h4 {\n  position: absolute;\n  bottom: 50px;\n  color: #fff;\n  transition: 0.2s ease;\n  transform: translateY(20px);\n  opacity: 0;\n}\n.work-slider-image .image-overlay a.h4:hover {\n  color: #008789;\n}\n.work-slider-image .image-overlay p {\n  position: absolute;\n  bottom: 15px;\n  color: #fff;\n  transform: translateY(10px);\n  opacity: 0;\n  transition: 0.3s ease;\n  transition-delay: 0.1s;\n}\n.work-slider-image:hover .image-overlay {\n  opacity: 1;\n}\n.work-slider-image:hover a.h4,\n.work-slider-image:hover p {\n  transform: translateY(0);\n  opacity: 1;\n}\n.work-slider .slick-dots {\n  padding-left: 0;\n  position: absolute;\n  bottom: -60px;\n  left: 50%;\n  transform: translateX(-50%);\n}\n.work-slider .slick-dots li {\n  margin: 0 14px;\n}\n@media (max-width: 400px) {\n  .work-slider .slick-dots li {\n    margin: 0 2px;\n  }\n}\n.work-slider .slick-dots li:first-child {\n  margin-left: 0;\n}\n.work-slider .slick-dots li button {\n  height: 10px;\n  width: 40px;\n  border-radius: 25px;\n  color: transparent;\n  overflow: hidden;\n  border: 0;\n  border: 2px solid #e5e5e5;\n  background: transparent;\n  outline: 0;\n  display: block;\n}\n.work-slider .slick-dots li button::before {\n  display: none;\n}\n@media (max-width: 400px) {\n  .work-slider .slick-dots li button {\n    width: 10px;\n  }\n}\n.work-slider .slick-dots li.slick-active button {\n  border-color: #008789;\n}\n\n/* 19. project section */\n.project {\n  /* 19.1 project menu */\n  /* 19.2 project item */\n}\n.project-menu ul li {\n  padding: 5px 20px;\n  border-radius: 35px;\n  color: #222;\n  font-size: 16px;\n  font-weight: 400;\n  border: 1px solid #e5e5e5;\n  cursor: pointer;\n  background: #f5f5f5;\n  margin: 0 10px 25px;\n}\n.project-menu ul li.active {\n  background: #008789;\n  color: #fff;\n}\n.project-info {\n  position: absolute;\n  top: 0;\n  left: 0;\n  height: 100%;\n  width: 100%;\n  background: rgba(0, 0, 0, 0.7);\n  opacity: 0;\n  transition: 0.2s ease;\n}\n.project-info a {\n  color: #fff;\n  position: absolute;\n  left: 30px;\n  bottom: 40px;\n  transform: translateY(20px);\n  opacity: 0;\n}\n.project-info a:hover {\n  color: #008789;\n}\n.project-info p {\n  position: absolute;\n  left: 25px;\n  bottom: 0;\n  color: #fff;\n  transition: 0.2s ease;\n  transform: translateY(20px);\n  opacity: 0;\n  transition-delay: 0.1s;\n}\n.project-info p i {\n  margin-right: 5px;\n}\n.project-info:hover {\n  opacity: 1;\n}\n.project-info:hover a {\n  transform: translateY(0);\n  opacity: 1;\n}\n.project-info:hover p {\n  transform: translateY(0);\n  opacity: 0.7;\n}\n\n/* 19.3 project single page start */\n/* 19.4 nav tabs */\n.nav-tabs {\n  border: 0;\n  margin-bottom: 30px;\n}\n@media (max-width: 991px) {\n  .nav-tabs {\n    justify-content: center;\n  }\n}\n.nav-tabs .nav-item:not(:last-child) {\n  margin-right: 10px;\n}\n@media (max-width: 575px) {\n  .nav-tabs .nav-item {\n    margin-bottom: 10px;\n  }\n}\n.nav-tabs .nav-item .nav-link {\n  padding: 10px 30px;\n  border: 1px solid #e5e5e5;\n  background: #f5f5f5;\n  border-radius: 45px;\n  text-align: center;\n  vertical-align: middle;\n  color: #222;\n  transition: 0.2s ease;\n  font-size: 16px;\n  font-family: \"Poppins\", sans-serif;\n}\n.nav-tabs .nav-item .active {\n  background: #fff;\n  color: #008789;\n  border-color: #008789;\n}\n\n/* 19.5 tab content */\n.tab-content {\n  padding: 40px;\n  margin-bottom: 60px;\n  border-radius: 5px;\n  background: #fff;\n  border: 1px solid #e5e5e5;\n}\n.tab-content-item {\n  margin-left: 20px;\n}\n.tab-content-item:not(:last-child) {\n  margin-bottom: 45px;\n}\n.tab-content-item h6 {\n  font-family: \"Open Sans\", sans-serif;\n  font-weight: 600;\n  font-size: 14px;\n  color: #222;\n  margin-bottom: 25px;\n  position: relative;\n}\n.tab-content-item h6::before {\n  position: absolute;\n  content: \"\\e65d\";\n  font-family: \"themify\";\n  top: 0;\n  left: -20px;\n  color: #008789;\n}\n.tab-content-item p {\n  margin-bottom: 0;\n}\n\n/* project single page end */\n/* 21.4.3 pagination */\n.pagination .page-item .page-link {\n  background: transparent;\n  border: none;\n  color: #000;\n  font-family: \"Open Sans\", sans-serif;\n  font-weight: 600;\n  transition: 0.3s ease;\n}\n.pagination .page-item .page-link:hover {\n  color: #008789;\n}\n.pagination .page-item.active .page-link {\n  color: #008789;\n}\n.pagination .prev,\n.pagination .next {\n  border: 1px solid #e5e5e5;\n  border-radius: 30px;\n  padding: 5px 10px;\n  transition: 0.3s ease;\n}\n.pagination .prev:hover,\n.pagination .next:hover {\n  border-color: #008789;\n}\n.pagination .prev:hover .page-link,\n.pagination .next:hover .page-link {\n  color: #008789;\n}\n.pagination .prev {\n  margin-right: 30px;\n}\n.pagination .next {\n  margin-left: 30px;\n}\n\n/* 21.4.1 post slider */\n.post-slider {\n  position: relative;\n}\n.post-slider .prevArrow,\n.post-slider .nextArrow {\n  position: absolute;\n  width: 50px;\n  height: 50px;\n  border-radius: 50%;\n  line-height: 50px;\n  top: 50%;\n  border: 0;\n  font-size: 20px;\n  background: transparent;\n  color: #008789;\n  z-index: 9;\n  background: rgba(255, 255, 255, 0.8);\n  transition: 0.3s ease;\n}\n.post-slider .prevArrow:focus,\n.post-slider .nextArrow:focus {\n  outline: 0;\n}\n.post-slider .prevArrow:hover,\n.post-slider .nextArrow:hover {\n  background: rgba(0, 135, 137, 0.25);\n}\n.post-slider .prevArrow {\n  left: 40px;\n}\n.post-slider .nextArrow {\n  right: 40px;\n}\n.post-slider .slick-disabled {\n  color: #e5e5e5;\n}\n\n/* 21.4.4 sidebar */\n.search-wrapper {\n  position: relative;\n}\n.search-wrapper input {\n  background: #f5f5f5;\n  border-radius: 30px;\n  font-weight: 400;\n  height: 40px;\n  line-height: 40px;\n  padding: 0 25px;\n  font-size: 13px;\n}\n.search-wrapper::before {\n  content: \"\\e610\";\n  font-family: \"themify\";\n  position: absolute;\n  top: 50%;\n  right: 20px;\n  color: #666;\n  transform: translateY(-50%);\n}\n\nul.tag-list li {\n  margin-bottom: 7px;\n}\nul.tag-list li a {\n  color: #666;\n  padding: 10px 15px;\n  display: block;\n  border: 1px solid #e5e5e5;\n  border-radius: 30px;\n  font-size: 13px;\n  transition: 0.3s ease;\n}\nul.tag-list li a:hover {\n  background: #008789;\n  border-color: #008789;\n  color: #fff;\n}\n\n.newsletter .form-control {\n  font-weight: 400;\n  padding: 0 20px;\n  height: 40px;\n  line-height: 40px;\n  border-radius: 30px;\n  font-size: 13px;\n}\n\n/* 21.5 blog single start */\n/* share icon */\n.share-icon {\n  height: 40px;\n  width: 40px;\n  border-radius: 50%;\n  line-height: 40px;\n  text-align: center;\n  font-size: 16px;\n  display: inline-block;\n}\n.share-icon i {\n  color: #fff;\n}\n\n/* 21.5.7 blog sidebar */\n.sidebar-box-shadow {\n  box-shadow: -3px 0px 3px 0px rgba(0, 0, 0, 0.06);\n}\n\n/* blog single end */\n/* 22. contact section */\n.form-control {\n  border-radius: 5px;\n  padding: 0 15px 2px;\n  height: 40px;\n  margin-bottom: 15px;\n  border: 1px solid #e5e5e5;\n  font-family: \"Open Sans\", sans-serif;\n  font-weight: 600;\n  font-size: 14px;\n  color: #666;\n}\n.form-control:focus {\n  border-color: #008789;\n  box-shadow: none !important;\n}\n.form-control::placeholder {\n  color: #666;\n}\n\n/* 22.3 google map */\n.map {\n  position: relative;\n}\n.map #map_canvas {\n  height: 450px;\n}\n\n/* 23.1 signup greetings */\n.signup {\n  border-radius: 5px;\n  overflow: hidden;\n  box-shadow: 0px 10px 35px 0px rgba(0, 0, 0, 0.1);\n  /* 23.2 signup form */\n}\n.signup-greeting {\n  background-position: top center;\n  background-size: cover;\n  background-repeat: no-repeat;\n  padding: 70px 0 140px;\n  text-align: center;\n}\n@media (max-width: 1199px) {\n  .signup-greeting {\n    padding-bottom: 80px;\n  }\n}\n@media (max-width: 767px) {\n  .signup-greeting {\n    padding-top: 200px;\n  }\n}\n@media (max-width: 575px) {\n  .signup-greeting {\n    padding-top: 400px;\n  }\n}\n.signup-greeting img {\n  margin-bottom: 50px;\n  position: relative;\n  z-index: 9;\n}\n.signup-greeting h4 {\n  color: #fff;\n  margin-bottom: 20px;\n  position: relative;\n  z-index: 9;\n}\n.signup-greeting p {\n  color: #fff;\n  position: relative;\n  z-index: 9;\n  margin: 0 40px;\n}\n.signup-form {\n  padding: 50px 50px 50px 35px;\n}\n@media (max-width: 1199px) {\n  .signup-form {\n    padding: 50px 15px 50px 0;\n  }\n}\n@media (max-width: 767px) {\n  .signup-form {\n    padding: 50px;\n  }\n}\n@media (max-width: 575px) {\n  .signup-form {\n    text-align: center;\n  }\n}\n.signup-form h4 {\n  float: left;\n  font-size: 26px;\n  margin-bottom: 35px;\n}\n@media (max-width: 575px) {\n  .signup-form h4 {\n    float: unset;\n  }\n}\n.signup-form a {\n  color: #008789;\n}\n.signup-form .btn {\n  margin-top: 30px;\n}\n.signup-form p.signup-with {\n  display: inline-block;\n  margin-top: 42px;\n}\n.signup-form ul {\n  margin-top: 37px;\n  display: inline-block;\n}\n.signup-form ul li a {\n  height: 30px;\n  width: 30px;\n  line-height: 30px;\n  color: #fff;\n  text-align: center;\n  border-radius: 50%;\n  display: inline-block;\n}\n\n/* 24. comming soon */\n.comming-soon {\n  height: 100vh;\n  background-size: cover;\n  display: flex;\n  align-items: center;\n}\n.comming-soon h1 {\n  font-size: 50px;\n  font-weight: 600;\n}\n@media (max-width: 575px) {\n  .comming-soon h1 {\n    font-size: 30px;\n  }\n}\n.comming-soon .form-control {\n  height: 45px;\n  border-radius: 45px;\n  position: relative;\n  background: transparent;\n  border: 1px solid #e5e5e5;\n  color: #fff;\n  margin-bottom: 40px;\n}\n.comming-soon .form-control::placeholder {\n  color: #666;\n}\n.comming-soon .btn-email {\n  position: absolute;\n  top: 0;\n  right: 15px;\n  background: #fff;\n  border: 0;\n  color: #008789;\n  border-radius: 45px;\n  height: 45px;\n  padding: 0 50px;\n}\n@media (max-width: 575px) {\n  .comming-soon .btn-email {\n    padding: 0 10px;\n  }\n}\n.comming-soon i {\n  color: #5c5c5c;\n  margin: 0 15px;\n  transition: 0.2s ease;\n  font-size: 24px;\n}\n.comming-soon i:hover {\n  color: #008789;\n}\n\n/* 24.1 syotimer */\n.syotimer {\n  text-align: center;\n  margin: 0 auto 40px;\n}\n.syotimer-cell:last-child .syotimer-cell__value::before {\n  opacity: 0;\n}\n.syotimer-cell {\n  display: inline-block;\n  margin: 0 25px;\n}\n@media (max-width: 1199px) {\n  .syotimer-cell {\n    margin: 0 20px;\n  }\n}\n@media (max-width: 575px) {\n  .syotimer-cell {\n    margin: 0 10px;\n  }\n}\n@media (max-width: 400px) {\n  .syotimer-cell {\n    margin: 0 5px;\n  }\n}\n.syotimer-cell__value {\n  font-size: 60px;\n  font-weight: 600;\n  color: #fff;\n  position: relative;\n}\n@media (max-width: 1199px) {\n  .syotimer-cell__value {\n    font-size: 50px;\n  }\n}\n@media (max-width: 575px) {\n  .syotimer-cell__value {\n    font-size: 30px;\n  }\n}\n@media