n\r\n/* project single page end */","/* 21.4.3 pagination */\r\n.pagination {\r\n  .page-item {\r\n    .page-link {\r\n      background: transparent;\r\n      border: none;\r\n      color: $black;\r\n      font-family: $secondary-font;\r\n      font-weight: 600;\r\n      transition: .3s ease;\r\n\r\n      &:hover {\r\n        color: $primary-color;\r\n      }\r\n    }\r\n\r\n    &.active {\r\n      .page-link {\r\n        color: $primary-color;\r\n      }\r\n    }\r\n  }\r\n\r\n  .prev,\r\n  .next {\r\n    border: 1px solid $border-color;\r\n    border-radius: 30px;\r\n    padding: 5px 10px;\r\n    transition: .3s ease;\r\n\r\n    &:hover {\r\n      border-color: $primary-color;\r\n\r\n      .page-link {\r\n        color: $primary-color;\r\n      }\r\n    }\r\n  }\r\n\r\n  .prev {\r\n    margin-right: 30px;\r\n  }\r\n\r\n  .next {\r\n    margin-left: 30px;\r\n  }\r\n}\r\n\r\n/* 21.4.1 post slider */\r\n.post-slider {\r\n  position: relative;\r\n\r\n  .prevArrow,\r\n  .nextArrow {\r\n    position: absolute;\r\n    width: 50px;\r\n    height: 50px;\r\n    border-radius: 50%;\r\n    line-height: 50px;\r\n    top: 50%;\r\n    border: 0;\r\n    font-size: 20px;\r\n    background: transparent;\r\n    color: $primary-color;\r\n    z-index: 9;\r\n    background: rgba($color: $white, $alpha: 0.8);\r\n    transition: .3s ease;\r\n\r\n    &:focus {\r\n      outline: 0;\r\n    }\r\n\r\n    &:hover {\r\n      background: rgba($color: $primary-color, $alpha: .25);\r\n    }\r\n  }\r\n\r\n  .prevArrow {\r\n    left: 40px;\r\n  }\r\n\r\n  .nextArrow {\r\n    right: 40px;\r\n  }\r\n\r\n  .slick-disabled {\r\n    color: #e5e5e5;\r\n  }\r\n}\r\n\r\n/* 21.4.4 sidebar */\r\n.search-wrapper {\r\n  input {\r\n    background: $bg-gray;\r\n    border-radius: 30px;\r\n    font-weight: 400;\r\n    height: 40px;\r\n    line-height: 40px;\r\n    padding: 0 25px;\r\n    font-size: 13px;\r\n  }\r\n\r\n  position: relative;\r\n\r\n  &::before {\r\n    content: \"\\e610\";\r\n    font-family: $icon-